
.btn-primary {
  background-color: #F5A623;
  border: 1px #DC951F;
  box-shadow: 0 0px 1px rgba(0, 0, 0, 0.2);
  color: #FFFFFF;
}
.btn-primary:hover, .btn-primary:focus, .btn-primary:not([disabled]):not(.disabled):active {
    background-color: #DC951F;
}
.page-privacy-policy {
  height: 100vh;
}



