
@media (max-width: 768px) {
.form-horizontal .form-group {
    margin-right: 0px;
    margin-left: 0px;
}
}
.btn-primary {
  background-color: #F5A623;
  border: 1px #DC951F;
  box-shadow: 0 0px 1px rgba(0, 0, 0, 0.2);
  color: #FFFFFF;
}
.btn-primary:hover, .btn-primary:focus, .btn-primary:not([disabled]):not(.disabled):active {
    background-color: #DC951F;
}
.form-group .input-group {
  width: 70%;
}
.help {
  display: block;
  font-size: 11px;
  margin-top: 5px;
}
.help.is-danger {
    color: #ff3860;
}
input.form-control.input {
  max-height: 3em;
}
input#file {
  max-height: 3em;
}
.input-group.feedback {
  display: block;
  height: 34px;
  line-height: 34px;
  text-align: center;
  text-align: left;
}
.modal-body {
  padding-right: 3e;
}
.alert {
  white-space: pre-wrap;
}



