
html,
body,
.container-table {
  height: 100%;
}
.container-table {
  color: white;
  display: table;
}
.container-table .vertical-10p {
    padding-top: 10%;
}
.container-table .vertical-20p {
    padding-top: 20%;
}
.container-table .logo {
    padding: 3em;
    width: 70%;
}
.container-table .over-particles {
    position: relative;
    width: 50%;
    z-index: 999;
}
.container-table .loginForm {
    background: rgba(255, 255, 255, 0.8);
    border-radius: 4px;
    padding: 16px;
}
.container-table .loginForm input {
      height: 4em;
}
.container-table .loginForm .input-group {
      height: 4em;
      padding-bottom: 1em;
}
.container-table .loginForm .input-group .input-group-addon {
        background-color: #3795db;
        border-color: #3795db;
}
.container-table .loginForm .input-group .input-group-addon i {
          color: #fff;
}
.container-table .loginForm .input-group .input-group-addon i.email {
            font-size: 1.2em;
}
.container-table .loginForm .input-group .input-group-addon i.password {
            font-size: 1.5em;
            width: 18px;
}
.btn-primary {
  background-color: #F5A623;
  border: 1px #DC951F;
  box-shadow: 0 0px 1px rgba(0, 0, 0, 0.2);
  color: #FFFFFF;
}
.btn-primary:hover, .btn-primary:focus, .btn-primary:not([disabled]):not(.disabled):active {
    background-color: #DC951F;
}



