
@media (max-width: 768px) {
.form-horizontal .form-group {
    margin-right: 0px;
    margin-left: 0px;
}
}
.form-group .input-group {
  width: 70%;
}
.help {
  display: block;
  font-size: 11px;
  margin-top: 5px;
}
.help.is-danger {
    color: #ff3860;
}
input.form-control.input {
  max-height: 3em;
}
input#file {
  max-height: 3em;
}
.input-group.feedback {
  display: block;
  height: 34px;
  line-height: 34px;
  text-align: center;
  text-align: left;
}
.modal-body {
  padding-right: 3e;
}



