
.bg-hamv-box {
  background: #3796dd;
  border-radius: 6px;
  color: #fff;
  min-height: 74px;
}
.bg-hamv-box .info-box-content {
    display: inherit;
    height: 74px;
    margin: 0 15px;
    padding: 0;
    text-align: center;
    vertical-align: middle;
}
.bg-hamv-box .info-box-content .hamv-info-box-icon {
      align-items: center;
      display: flex;
      height: 100%;
      padding-left: 0;
      padding-right: 0;
}
.bg-hamv-box .info-box-content .hamv-info-box-icon i {
        align-items: center;
        background: #0275b3;
        border-radius: 50%;
        color: #fff;
        display: flex;
        font-size: 28px;
        height: 48px;
        justify-content: center;
        margin: 0 auto;
        width: 48px;
}
.bg-hamv-box .info-box-content .hamv-info-box-label {
      align-items: center;
      display: flex;
      flex-wrap: wrap;
      height: 100%;
      justify-content: center;
      padding-left: 0;
      padding-right: 0;
}
.bg-hamv-box .info-box-content .hamv-info-box-label .info-box-text {
        font-size: 14px;
}
.bg-hamv-box .info-box-content .hamv-info-box-label .info-box-number {
        font-size: 22px;
        font-weight: normal;
        line-height: 110%;
}



